.scroll-to-top-button {
    background-color: #2196f3;
   border-radius: 50%;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 20px;
    right: 20px;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.5s;
  }
  
  .scroll-to-top-button .fas {
    color: gray;
    font-size: 24px;
  }
  
  .show-scroll-to-top-button {
    opacity: 1;
  }